.loading {
 position:relative;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}


.quotesAnimation{
  animation-name: quotesItem;
animation-duration: 1s;
}

@keyframes quotesItem {
  0%   {opacity:  0%;}
  25%  {opacity:  25%;}
  50%  {opacity:  50%;}
  100% {opacity:  100%;}
  
}